import React from "react";
import { Link } from "react-router-dom";
import { getCartProducts } from "../repository";
import CartItem from "./CartItem";
import "bootstrap/dist/css/bootstrap.min.css";
import swal from "sweetalert";
import {
  FaCartArrowDown,
  FaUserPlus,
  FaBars,
  FaCheckSquare,
  FaStore,
  FaSeedling,
  FaSearch,
  FaLock
} from "react-icons/fa";
export default class Cart extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      total: 0
    };
  }

  componentWillMount() {
    let cart = localStorage.getItem("cart");
    console.log(cart);
    if (!cart) return;
    getCartProducts(cart).then(products => {
      let total = 0;
      for (var i = 0; i < products.length; i++) {
        total += products[i].price * products[i].qty;
      }
      this.setState({ products, total });
    });
  }

  removeFromCart = product => {
    swal("Are You sure you want to remove product?", {
      buttons: {
        cancel: "default",
        ok: {
          text: "Remove",
          value: "ok"
        },
        cancel: true
      }
    }).then(value => {
      switch (value) {
        case "ok":
          let products = this.state.products.filter(
            item => item.id !== product.id
          );
          let cart = JSON.parse(localStorage.getItem("cart"));
          delete cart[product.id.toString()];
          localStorage.setItem("cart", JSON.stringify(cart));
          let total = this.state.total - product.qty * product.price;
          this.setState({ products, total });
          swal("Product has been removed!");
          break;

        default:
          swal("Your Products Are Safe!!", "", "success");
          break;
      }
    });
  };

  clearCart = () => {
    localStorage.removeItem("cart");
    this.setState({ products: [] });
    swal("Your Cart Has Been Cleared!!!", "", "success");
  };

  render() {
    const { products, total } = this.state;
    return (
      <div class="container mt-5 ">
        <h5 style={lp}>
          Cart <FaCartArrowDown />
        </h5>
        <hr />
        {products.map((product, index) => (
          <CartItem
            product={product}
            remove={this.removeFromCart}
            key={index}
          />
        ))}

        {products.length ? (
          <div>
            <h5 class="text-dark">
              Total Amount:
              <span className="float-right text-dark">₦{total}</span>
            </h5>
            <hr />
          </div>
        ) : (
          ""
        )}

        {!products.length ? (
          <h3 className="text-dark">No item on the cart!!!</h3>
        ) : (
          ""
        )}
        <Link to="/checkout">
          <button className="btn btn-sm btn-outline-success float-right">
            <b>Checkout</b>
          </button>
        </Link>
        <button
          className="btn btn-outline-danger btn-sm float-left"
          onClick={this.clearCart}
          style={{ marginRight: "10px" }}
        >
          <b>Clear Cart</b>
        </button>
        <br />
        <br />
        <br />
        <br />
      </div>
    );
  }
}

var lp = {
  fontSize: "20px"
};
